@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: "Junegull";
    font-weight: 400;
    font-style: normal;
    src: local("Junegull"), url(./assets/fonts/Junegull.ttf) format("truetype");
}

