@import "../../assets/style/app";

.tookanomix {
  @include container;
  background-image: url(../../assets/jpeg/grid.jpg);
  padding-top: 16px;
  padding-bottom: 16px;

  @include desktop {
    padding-top: 0;
    padding-bottom: 0;
  }

  .inner {
    @include inner;

    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      flex-direction: row;
      align-items: flex-end;
      justify-content: space-between;
    }

    .item {
      display: flex;
      flex-direction: column;
      align-items: center;

      & img {
        display: block;
      }

      &:nth-child(1) {
        order: 1;

        @include desktop {
          order: 2;
        }

        img {

          &:nth-child(1) {
            width: 272px;
            height: auto;

            @include desktop {
              width: 600px;
            }
          }

          &:nth-child(2) {
            width: 150px;
            height: auto;

            @include desktop {
              width: 330px;
            }
          }

        }

      }

      &:nth-child(2) {
        margin-top: 32px;
        order: 2;

        @include desktop {
          margin-top: 0;
          order: 1
        }


        img {

          &:nth-child(1) {
            width: 140px;

            @include desktop {
              width: 300px;
            }
          }

          &:nth-child(2) {
            width: 100px;

            @include desktop {
              width: 215px;
            }
          }

        }

      }

      &:nth-child(3) {
        margin-top: 32px;
        order: 3;

        @include desktop {
          margin-top: 0;
        }

        img {

          &:nth-child(1) {
            width: 140px;

            @include desktop {
              width: 300px;
            }
          }

          &:nth-child(2) {
            width: 100px;

            @include desktop {
              width: 215px;
            }
          }

        }

      }


    }


  }

}